html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FEFCFC;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FEFCFC;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-amarillo-contacto {
  background-color: #E08709;
  color: #FEFCFC;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo-contacto:hover {
  background-color: #c77808;
  cursor: pointer;
}

.header {
  background-image: url("/src/img/fondo.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 70rem;
  background-position: center center;
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo h1 {
  color: #FEFCFC;
  font-weight: 400;
}
.logo .natural {
  color: #71B100;
  font-weight: bold;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
.nav a {
  color: #FEFCFC;
  font-size: 1.8rem;
  font-weight: 400;
  text-align: center;
}

.texto-header {
  margin-top: 15rem;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  align-content: center;
  justify-content: center;
  align-items: baseline;
  justify-items: center;
}
.texto-header h1 {
  color: #FEFCFC;
  font-weight: bold;
  font-size: 5rem;
  text-align: center;
  margin: 0;
  grid-row: 1;
}
.texto-header span {
  color: #71B100;
}
.texto-header p {
  color: #FEFCFC;
  text-align: center;
  grid-row: 2;
}

.boton_header_grande {
  background-color: #FEFCFC;
  padding: 2rem;
  border-radius: 5px;
  text-decoration: none;
  color: #333333;
  text-align: center;
  margin-left: auto;
  margin-right: 2rem;
}

.boton_header_chico {
  border: 1px solid #FEFCFC;
  color: #FEFCFC;
  border-radius: 5px;
  padding: 2rem;
  text-align: center;
  margin-left: 2rem;
  margin-right: auto;
}

.barra_inf {
  justify-items: center;
  grid-row: 3;
  display: flex;
}

.nosotros {
  background-color: #BED191;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 5rem;
  margin-right: auto;
  margin-left: 0;
  width: 100%;
}

.icono {
  height: 4rem;
}

.icono2 {
  height: 4rem;
}

.nosotros_individual {
  height: 30rem;
  margin-right: 2rem;
  margin-left: 2rem;
  padding-top: 2rem;
}
.nosotros_individual h2 {
  color: #FEFCFC;
  margin-top: 0;
  font-size: 1.8rem;
  font-weight: bold;
}
.nosotros_individual h2::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #FEFCFC;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
}
.nosotros_individual p {
  color: #FEFCFC;
  font-size: 1.5rem;
  text-align: left;
}

.contenedor2 {
  width: 100%;
  max-width: 140rem;
  margin: 0 auto;
}

.cartel {
  position: absolute;
  top: 121rem;
  left: 0;
  color: #FEFCFC;
  border-radius: 0.5rem;
  height: 20rem;
  width: 45rem;
  z-index: 2;
  background-color: #333333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cartel h3 {
  font-family: "Pacifico", cursive;
  font-size: 3.5rem;
}

.img_piel {
  margin-top: 5rem;
  height: 45rem;
  z-index: 3;
}

.contenedoresp {
  width: 100%;
  max-width: 135rem;
  margin: 0 auto;
}

.btn_piel {
  display: inline-block;
  border-radius: 0.5rem;
  font-size: 1.8rem;
  background-color: #FEFCFC;
  padding: 2rem;
  color: #000000;
  font-family: "Pacifico", cursive;
}

.tarjeta1 {
  background-image: url("/src/img/pro1.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 40rem;
  height: 45rem;
}